exports.push([module.id, "\n.input-file {\n    position: absolute;\n    top: 0;\n    left: 0;\n    opacity: 0;\n    padding: 14px 0;\n    cursor: pointer;\n}\n.button-label {\n    margin-left: 16px;\n}\n", "", {"version":3,"sources":["/Users/arnaud/Documents/projects/school/frameworks-js/vuetd3/src/vue/components/src/vue/components/movieform.vue"],"names":[],"mappings":";AAmQA;IACA,mBAAA;IACA,OAAA;IACA,QAAA;IACA,WAAA;IACA,gBAAA;IACA,gBAAA;CACA;AAEA;IACA,kBAAA;CACA","file":"movieform.vue","sourcesContent":["<template>\n    <v-flex xs12 sm8>\n        <h1>{{renderTitle}}</h1>\n        <v-form @submit.prevent=\"handleSubmit\" ref=\"form\">\n            <v-text-field\n                    name=\"title\"\n                    label=\"Titre\"\n                    v-model=\"movie.title\"\n                    :rules=\"titleRules\"\n                    required\n            ></v-text-field>\n            <v-select\n                    name=\"genre\"\n                    label=\"Genre\"\n                    :items=\"genres\"\n                    v-model=\"movie.genre\"\n                    item-value=\"text\"\n            ></v-select>\n            <v-text-field\n                    name=\"director\"\n                    label=\"Ralisateur\"\n                    v-model=\"movie.director\"\n                    required\n            ></v-text-field>\n            <v-text-field\n                    name=\"directorYear\"\n                    label=\"Anne naissance ralisateur\"\n                    v-model=\"movie.directorYear\"\n                    required\n                    type=\"number\"\n            ></v-text-field>\n            <v-select\n                    name=\"directorCountry\"\n                    label=\"Pays ralisateur\"\n                    :items=\"countries\"\n                    v-model=\"movie.directorCountry\"\n                    item-value=\"text\"\n            ></v-select>\n            <v-flex>\n                <label for=\"datepicker\">Date de sortie</label>\n            </v-flex>\n            <v-date-picker\n                    id=\"datepicker\"\n                    v-model=\"movie.date\"\n            ></v-date-picker>\n\n            <v-text-field\n                    name=\"synopsis\"\n                    label=\"Synopsis\"\n                    v-model=\"movie.synopsis\"\n                    required\n                    :key=\"previousMovie.synopsis\"\n                    :rules=\"synopsisRules\"\n                    :counter=\"500\"\n                    multi-line\n            ></v-text-field>\n            <v-layout column>\n                <label for=\"file\">Affiche du film</label>\n                <v-flex xs12 sm6>\n                    <img :src=\"imageSrc\" style=\"max-height: 400px\">\n                </v-flex>\n                <v-flex xs12>\n                    <v-layout column align-center>\n                        {{inputFileContent}}\n                        <v-btn raised color=\"primary\">\n                            <v-icon right dark>attachment</v-icon>\n                            <span class=\"button-label\">\n                                Slctionner un fichier\n                            </span>\n                            <input\n                                    class=\"input-file\"\n                                    id=\"file\"\n                                    name=\"image\"\n                                    type=\"file\"\n                                    accept=\"image/*\"\n                                    v-on:change=\"uploadImage\"\n                                    :rules=\"imageRules\"\n                            />\n                        </v-btn>\n                    </v-layout>\n                </v-flex>\n            </v-layout>\n            <label for=\"rate\">Note</label>\n                <star-rating\n                        :key=\"previousMovie.rate\"\n                        :defaultValue=\"previousMovie.rate\"\n                        v-model=\"movie.rate\"\n                        id=\"rate\"\n                            name=\"rate\"\n                ></star-rating>\n            <v-btn\n                    raised\n                    color=\"info\"\n                    type=\"submit\"\n            >\n                {{submitBtnText}}\n            </v-btn>\n        </v-form>\n    </v-flex>\n</template>\n\n<script>\n    import { mapActions, mapGetters } from 'vuex';\n\n    const defaultImage = require('../../static/placeholder.png');\n    const redirectTo = (router, name, params) => {\n        router.push({\n            name,\n            params,\n        })\n    };\n\n    const formRules = {\n        titleRules: [\n            v => !!v || 'Titre obligatoire',\n            v => (v && v.length >= 2) || 'Le titre doit faire au moins 2 caractres',\n        ],\n        synopsisRules: [\n            v => !!v || 'Synopsis obligatoire',\n            v => (v && v.length >= 10) || 'Le synopsis doit faire au moins 10 caractres',\n            v => (v && v.length <= 500) || 'Le synopsis fait 500 caractres maximum',\n        ],\n        imageRules: [\n            v => !!v || 'Image obligatoire',\n        ],\n    };\n\n    const genres = {\n        genres: [\n            {text: 'Action'},\n            {text: 'Science-fiction'},\n            {text: 'Horreur'},\n            {text: 'Guerre'},\n            {text: 'Thriller'},\n            {text: 'Amour'},\n        ],\n    };\n\n    const countries = {\n        countries: [\n            {text: 'France'},\n            {text: 'Angleterre'},\n            {text: 'US'},\n        ],\n    };\n\n    export default {\n        props: ['isEditForm'],\n        data: () => ({\n            inputFileContent: '',\n            imageUploaded: false,\n            previousMovie: {},\n            movie: {},\n            imageSrc: null,\n            movieImageName: '',\n            ...formRules,\n            ...countries,\n            ...genres,\n        }),\n\n        methods: {\n\n            ...mapActions([\n                'getMovie',\n                'editMovie',\n                'addMovie',\n            ]),\n            uploadImage(e) {\n                const {files} = e.target;\n                if (files.length === 0) {\n                    return;\n                }\n                const reader = new FileReader();\n                reader.onload = (e) => {\n                    this.imageSrc = e.target.result;\n                    this.imageUploaded = true;\n                    this.inputFileContent = `Fichier choisi : ${files[0].name}`;\n                };\n                reader.readAsDataURL(files[0]);\n            },\n            handleSubmit(e) {\n                if (this.$refs.form.validate()) {\n                    const movie = new FormData(e.target);\n                    movie.append('genre', this.movie.genre);\n                    movie.append('date', this.movie.date);\n                    movie.append('directorCountry', this.movie.directorCountry);\n                    if (this.isEditForm) {\n                        movie.append('id', this.movie.id);\n                        if (!this.imageUploaded) {\n                            movie.append('image', this.previousMovie.image);\n                        }\n                        this.editMovie(movie).then(() => {\n                            redirectTo(this.$router, 'Movies',{snackbar:true, snackbarText: 'Film modifi'});\n                        });\n                    } else {\n                        this.addMovie(movie);\n                        redirectTo(this.$router, 'Movies',{snackbar:true, snackbarText: 'Film ajout'});\n                    }\n                    this.$refs.form.reset();\n                }\n            },\n            fillFormFields() {\n                for (const [index, data] of Object.entries(this.movie)) {\n                    this.movie[index] = data;\n                }\n            },\n            initForm(){\n                if(this.getMovieById(this.$route.params.id) === undefined){\n                    fetch(\"/api/movie?id=\" + this.$route.params.id).then(res => res.json())\n                        .then(obj => {\n                            console.log('OBJ',obj);\n                            this.previousMovie = obj;\n                            this.imageSrc = require(`../../static/uploads/${this.previousMovie.image}`);\n                            this.movie = Object.assign({}, this.previousMovie );\n                        })\n                        .catch(err => {\n                            console.warning(\"fail api : \", err)\n                        })\n                }else {\n                    this.previousMovie = this.getMovieById(this.$route.params.id);\n                    this.imageSrc = require(`../../static/uploads/${this.previousMovie.image}`)\n                    this.movie = Object.assign({}, this.previousMovie );\n                }\n                this.fillFormFields();\n            }\n        },\n        computed: {\n            ...mapGetters([\n                'getMovieById'\n            ]),\n            renderTitle() {\n                    return this.isEditForm ? `diter le film : ${this.previousMovie.title}` : 'Ajouter un film';\n            },\n            submitBtnText() {\n                return this.isEditForm ? 'Modifier' : 'Ajouter';\n            }\n        },\n        mounted() {\n            console.log('mounted')\n            if (this.isEditForm) {\n                console.log('editform')\n                this.initForm();\n            } else {\n                this.imageSrc = defaultImage;\n            }\n        },\n\n        // Because Vue doesn't re-render component on route change between edit and new movie\n        beforeRouteLeave(to, from, next) {\n            this.$refs.form.reset();\n            this.imageSrc = defaultImage;\n            this.previousMovie = {};\n            next();\n        },\n    };\n\n</script>\n\n<style>\n    .input-file {\n        position: absolute;\n        top: 0;\n        left: 0;\n        opacity: 0;\n        padding: 14px 0;\n        cursor: pointer;\n    }\n\n    .button-label {\n        margin-left: 16px;\n    }\n</style>\n"],"sourceRoot":""}]);

