          timeout: 6000,\n                text: 'Film ajout !'\n            };\n        },\n        computed: mapGetters([\n            'search_movies',\n            'moviesExist',\n        ]),\n\n        methods: {\n            ...mapActions([\n                'getMovies',\n            ]),\n\n            beforeEnter(el) {\n                el.style.opacity = 0;\n            },\n            enter(el, done) {\n                const delay = el.dataset.index * 150;\n                setTimeout(() => {\n                    Velocity(\n                        el,\n                        {opacity: 1},\n                        {complete: done},\n                    );\n                }, delay);\n            },\n            leave(el, done) {\n                const delay = el.dataset.index * 150;\n                setTimeout(() => {\n                    Velocity(\n                        el,\n                        {opacity: 0},\n                        {complete: done},\n                    );\n                }, delay);\n            },\n        },\n        mounted() {\n            this.getMovies();\n            const {\n                snackbar,\n                snackbarText,\n            } = this.$route.params;\n            if(snackbar){\n                this.snackbar = true;\n                this.snabckbarText  = snackbarText;\n            }\n        },\n    };\n\n</script>\n\n<style>\n    .movies {\n        display: flex;\n        flex-direction: row;\n        flex-wrap: wrap;\n        align-items: flex-start;\n    }\n</style>\n"],"sourceRoot":""}]);

