exports.push([module.id, "\n.movie-poster {\n    max-height: 300px;\n    max-width: 350px;\n    margin: 0 8px;\n}\n.movie-synopsis {\n    margin-top: 32px;\n    max-width: 400px;\n}\n\n", "", {"version":3,"sources":["/Users/arnaud/Documents/projects/school/frameworks-js/vuetd3/src/vue/components/src/vue/components/MovieDetails.vue"],"names":[],"mappings":";AAyHA;IACA,kBAAA;IACA,iBAAA;IACA,cAAA;CACA;AAEA;IACA,iBAAA;IACA,iBAAA;CACA","file":"MovieDetails.vue","sourcesContent":["<template>\n    <v-container justify-start>\n        <h1>{{ movie.title }}</h1>\n        <v-layout row wrap>\n            <div>\n                <div>\n                    <img\n                            class=\"movie-poster\"\n                            :src=\"imageSrc\"\n                    />\n                </div>\n                <div class=\"mt-3 mb-3\">\n                    <p class=\"subheading mb-0 ml-2\">Votre note</p>\n                    <star-rating\n                            :key=\"movie.rate\"\n                            :defaultValue=\"movie.rate\"\n                            v-model=\"movie.rate\"\n                            disabled\n                            id=\"rate\"\n                            name=\"rate\"\n                            ref=\"rate\"\n                    ></star-rating>\n                </div>\n                <router-link :to=\"{path: `/movie/${movie.id}/edit`}\" tag=\"span\">\n                    <v-btn color=\"primary\">\n                        Modifier\n                    </v-btn>\n                </router-link>\n                <router-link :to=\"{path: `/movie/${movie.id}/edit`}\" tag=\"span\">\n                    <v-btn color=\"error\" @click.stop=\"dialog = true\">\n                        Supprimer\n                    </v-btn>\n                </router-link>\n            </div>\n\n            <v-layout column class=\"ml-3\">\n                <div>\n                    <span class=\"grey--text subheading\">Date de sortie</span> <span>{{movie.date}}</span><br>\n                    <span class=\"grey--text subheading\">Ralis par</span> <span>{{movie.director}}</span> <span\n                        class=\"caption grey--text\">({{movie.directorCountry}}, N en {{movie.directorYear}})</span><br>\n                    <span class=\"grey--text subheading\">Genre</span> <span>{{movie.genre}}</span><br>\n                </div>\n                <div class=\"movie-synopsis\">\n                    <h3 class=\"headline mb-2\">Synopsis</h3>\n                    <p>\n                        {{movie.synopsis}}\n                    </p>\n                </div>\n            </v-layout>\n        </v-layout>\n        <v-dialog v-model=\"dialog\" max-width=\"500px\">\n            <v-card>\n                <v-card-title>\n                    <div>\n                        <h4>Suppression du film {{movie.title}}</h4>\n                        <p style=\"margin-top:16px\">Etes-vous sur de vouloir supprimer le film ? </p>\n                    </div>\n                </v-card-title>\n                <v-card-actions>\n                    <v-btn color=\"error\" @click=\"remove\">Supprimer</v-btn>\n                    <v-btn @click.stop=\"dialog=false\">Annuler</v-btn>\n                </v-card-actions>\n            </v-card>\n        </v-dialog>\n    </v-container>\n</template>\n\n<script>\n    import {mapGetters, mapActions} from 'vuex';\n\n    const redirectTo = (router, name, params) => {\n        router.push({\n            name,\n            params,\n        })\n    };\n\n    export default {\n        data() {\n            return {\n                movieId: this.$route.params.id,\n                imageSrc: '',\n                movie: {\n                    director: {}\n                },\n                dialog: false,\n            };\n        },\n        methods: {\n            ...mapActions([\n                'getMovie',\n                'deleteMovie',\n            ]),\n            remove() {\n                this.deleteMovie(this.movie);\n                redirectTo(this.$router, 'Movies',{snackbar:true, snackbarText: 'Film supprim'});\n            }\n        },\n        computed: {\n            ...mapGetters([\n                'getMovieById',\n            ]),\n        },\n        mounted() {\n            this.movieId = this.$route.params.id;\n            const movieFromStore = this.getMovieById(this.movieId);\n            if (movieFromStore) {\n                this.movie = movieFromStore;\n                this.imageSrc = require(`../../static/uploads/${this.movie.image}`);\n            } else {\n                this.getMovie(this.movieId)\n                    .then((movie) => {\n                        this.movie = movie;\n                        this.imageSrc = require(`../../static/uploads/${this.movie.image}`);\n                    });\n            }\n        }\n    };\n</script>\n\n<style>\n    .movie-poster {\n        max-height: 300px;\n        max-width: 350px;\n        margin: 0 8px;\n    }\n\n    .movie-synopsis {\n        margin-top: 32px;\n        max-width: 400px;\n    }\n\n</style>\n"],"sourceRoot":""}]);

